.evs__icons {
    display: flex;
    align-items: center;
    padding: 20px 10px;
}

.evs__icons > span {
   padding:5px 10px;
   margin: 5px;
   border-radius: 35px;
   font-size: 11px;
   font-weight: 600;
   text-transform: uppercase;
   color: var(--uccmain);
   box-shadow: 0 0 2px var(--uccmain) inset;
   cursor: pointer;
}

.evs__info img {
   height: 20px;
}

span.vote_cover { 
   background:var(--uccaccent-dark);
   color: #fff;
   font-size: 15px;
   box-shadow:none;
   padding: 5px 25px;
}

.monitor {
    padding: 8px !important;
    background: var(--uccmain-light);
    box-shadow: 2px -4px 3px #ccc inset!important;
    border: 2px solid var(--uccaccent-dark);
}


.monitor_icon {
   height: 20px;
   width: 20px;
}

.evs__info {
   display:flex;
   flex-direction: column;
   align-items: flex-start;
   padding: 5px 15px;
   font-size:12px;
   font-weight: 600;
   text-transform: uppercase;
   border-radius:10px;
   box-shadow: 0 0 1px var(--uccmain) inset;
   background: var(--uccmain-light) ;
   color: var(--uccaccent-dark);
}

.evs__info b {
   display: inline-block;
   width: 60px;
   font-weight: 600;
   color: var(--uccmain);
}

.trials {
   padding: 15px; 
}

.trials h3 {
   padding: 2px 10px;
   font-size: 15px;
   font-weight: 600;
   letter-spacing: 0.3em;
   text-align: center;
   color: #fff;
   background-color: var(--uccmain)
}

.trials p {
   font-family:Verdana, Geneva, Tahoma, sans-serif;
   padding: 10px;
   font-size: 0.6em;
   font-weight: 600;
   letter-spacing:0.1em;
   text-transform: uppercase;
   color: var(--uccaccent);
   border: thin solid var(--uccaccent);
   border-radius:5px;
}