@import url(../../assets/css/ui-auth-elements.css);
@import url(../../assets/css/ui-index.css);

.layout-login__inner, .layout-login  {
    background: var(--uccmain) !important;
}

.layout-login__form {
    border-left: 3px solid var(--uccmain-light);
    background: var(--uccmain);
}

.layout-login .footer, .login-form__head {
   background: linear-gradient( var(--uccmain) 0%,var(--uccmain) 100%);
}

.layout-login__link, .layout-login__link:visited, .layout-login__link:active {
    color: var(--uccmain-light);
}

.layout-login__link:hover {
    color: var(--uccsec);
}

.layout-login__text {
    color: var(--uccsec);
}

.login-form__link {
    color: var(--uccsec);
}

.signin-form__forgot-password {
    color: var(--uccsec) !important;
}

.ae-button__green {
    background: var(--uccaccent-dark);
}

.ae-button__green:enabled:hover {
    background: var(--uccsec);
    color: #000;
}

.layout-login__inner input[type=text], .layout-login__inner input[type=email], .layout-login__inner input[type=password], .layout-login__inner input[type=date], .layout-login__inner input::placeholder,.layout-login__inner select,.layout-login__inner option {
    color: var(--uccmain-light) !important;
}

.layout-login__reset {
    border-left: 3px solid var(--uccmain-light) !important;
    border-right: 3px solid var(--uccmain-light) !important;
    border-top: 3px solid var(--uccmain-light) !important;
    background: var(--uccmain) !important;
}

.login_hr {
    border-top: 1px dashed var(--uccsec);
}

.gauth {
    border-color: #343434;
}

.gauth__button {
    display: flex;
    align-items: center;
    justify-content:center;
}

.gauth__icon {
    height: 30px;
    width: 30px;
    margin-right: 15px;
}

.gauth b{
    font-weight: 600;
    color: var(--uccmain);
    text-shadow: 0 -1px 2px var(--uccmain-light) inset;
}