@import url(../../assets/css/ui-vendor.css);
@import url(../../assets/css/ui-style.css);


body {
   background-color: var(--uccprimary-color);
   -ms-overflow-style: none;  /* IE and Edge */
   scrollbar-width: none;  /* Firefox */
}

/* Chrome, Safari and Opera */
body::webkit-scrollbar {
   display: none;
}

h2, h3 {
   /**/ font-family: Roboto,Verdana, Geneva, Tahoma, sans-serif !important;
   font-weight:bolder;
}


/* Topbar Styling */
.topbar {
    background-color:var(--ucctopbar);
}

.sub-head {
    background-color:var(--uccmain);
    line-height: 40px;
}

.sub-head > span {
   font-size:.9rem;
   color:#fff;
   text-shadow:0 0 5px var(--uccmain-dark);
}

.card-token {
    background-image: linear-gradient(45deg, var(--ucctopbar) 0%, var(--uccmain) 100%);
    color: rgb(255, 255, 255);
}

.card-token .card-sub-title {
    color: var(--uccsec);
}

.profile__cover div:nth-child(1),.profile__cover div:nth-child(3){
   background-color: var(--uccaccent);
}

.profile__cover div:nth-child(2){
    background-color: var(--uccsec);
 }


.profile__nav {
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0 0 4px 1px #ccc inset ;
    background-color: #fff;
    
}

.data-head th {

   background: var(--uccsec);
   color:var(--uccmain) !important;
   padding: 5px 10px 5px !important;
   margin-bottom: 10px !important;
   border-bottom: 2px solid var(--uccmain-dark) !important;
   border-radius:5px;
}

.data-col {
   display:flex;
   align-items:center;
   justify-content:center;
   padding: 5px;
   border-bottom: none;

}

.data-col > a{
   width: 155px;
   font-size: 12px;
   font-weight:bolder;
   text-align:center;
   padding: 5px 18px;
   border-radius: 25px;
   background: #ccc;
   box-shadow: 0 0 1px #000;
   color: var(--uccmain) !important;
}

.data-col > a.active{
    font-size: 11px;
    font-weight:bolder;
    padding: 5px 18px;
    border-radius: 25px;
    background: var(--uccmain);
    color: #fff !important;
 }

.token-padding {
   border-color: var(--uccmain);
 }

 .app-id {
   background: var(--uccmain);
   color: var(--uccsec);
 }

 .bread-crum {
     display: flex;
     align-items: center;
 }

 .bread-crum li {
    display: flex;
    flex-grow: initial;
    align-items: center;
    
 }

 .bread-crum li a.active {
    background:  var(--uccmain);
    border-color: var(--uccmain);;
 }

 .bread-crum li a {
    color:  var(--uccmain);
    border-color: var(--uccmain);;
 }

 .unit-cover {
    display:flex;
    align-items: center;
    justify-content: center;
    background: var(--uccmain);
    color: var(--uccsec);
    text-align: center;
    font-weight: bolder;
  }

  .row {
      margin-right: 0;
      margin-left: 0;
  }

  .content-area {
    background: #e1e4d836;
    border-radius: 8px;
    border: none;
    box-shadow: 0px 0px 3px #bbb inset;
  }

  .token-currency-choose, .token-contribute, .token-overview-wrap {
    max-width: 350px;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .nitro .btn-primary {
    background: var(--uccmain);
    border-color: var(--uccmain);
  } 

  .nitro > .text-info {
    color: var(--uccaccent-dark) !important;
  }

  .btn-outline.btn-light {
    color: rgb(52 52 52);
    border-color: rgb(52 52 52);
    background: var(--uccsec)  !important;
  } 

  .btn-dark {
    background: var(--uccmain);
    border-color: var(--uccmain);
  }

  .btn-dark:disabled, .btn-dark:hover, .btn-dark:focus, .btn-dark:active, .btn-dark.active {
    background: var(--uccmain);
    border-color: var(--uccmain);
  }  

  .btn-light-alt, .btn-light-alt:hover {
    color:  var(--uccmain-dark) !important;
    background: var(--uccsec);
    border-color: var(--uccsec);
    text-shadow: -1px -1px 2px var(--uccmain-dark) inset;
  }


  .text-info {
    color: var(--uccmain) !important;
  }

  .btn {
    position: relative;
    color: #fff;
  }
  

  .btn-primary {
    background: var(--uccaccent-dark);
    border-color: var(--uccaccent-dark);
  }

  .btn-primary:disabled, .btn-primary:hover, .btn-primary:focus, .btn-primary:active {
    color: rgb(255, 255, 255);
    background: var(--uccmain);
    border-color: var(--uccmain);
  }

  .btn-warning, .btn-warning:hover {
    color:  var(--uccmain-dark) !important;
    background: var(--uccsec);
    border-color: var(--uccsec);
    text-shadow: -1px -1px 2px var(--uccmain-dark) inset;
   }
  
  .card-sub-title {
    color: var(--uccmain);
    font-size:15px;
    font-weight: 600;
  }

  .navbar-menu>li>a {
    color: var(--uccmain);
    border-color: var(--uccmain);
  }

  .token-statistics .card-innr {
    padding : 16px 20px;
  }

  .data-details-list, .data-details {
    border: none;
    box-shadow: 0 0 4px #ddd inset;
    background: #fdfdfd;
  }

  .data-details-head {
    font-weight:bolder; 
  }

  .data-details-des {
    border: none;
  }


  /* Styling Admin CRUD Pages */
  .admin__cover > .data-col {
    flex: initial !important;
  }

  .admin__cover .data-head th {
    background: none;
    color:inherit;
    margin-bottom: 0 !important;
    border-bottom: none !important;
    border-radius:0 !important;
 }

 .input-item-label {
     color: var(--uccmain-dark);
 }

 .input-bordered {
    background-color: var(--uccmain-light) !important;
    border: none !important;
    box-shadow: 0 0 4px #bbb inset;
    border-radius: 25px;
    color: var(--uccaccent-dark);
    font-size: 15px;
    font-weight: bolder;

 }

 .admin-tnx {
    padding: 15px !important;
    background: #fff;
}

.data-head > .data-td {
  box-shadow: 0;
}

.data-td {
  padding: 10px 7px;
  line-height:1.1rem;
  /*border-bottom: thin solid #eee;*/
  box-shadow: 0 -1px 1px #f3f3f3 inset;
}

.small {
  color: var(--uccmain-dark) !important;
}

.card-innr {
  padding: 16px 20px;
}

@media(max-width:600px) {

  .main-content, .container {
    width: 100%;
    padding:0;
  }

  .topbar .container {
    padding: 5px 10px;
  }

  .sub-head {
    line-height:1.5em;
    font-size: 0.9em;
  }

  .portfolio {
    padding:0 2px !important;
  }

  .app-cover, .unit-cover {
    display: none;
  }

  .portfolio_title {
    font-size:1.2em !important;
  }

  .token-balance {
    display: flex !important;
    flex-direction:column!important;
    align-items:center!important;
    justify-content:center!important;
  }

  .token-balance .lead {
    display:block;
    margin: 5px auto;
    text-align:center!important;
  }

  .vote-btn {
    padding: 10px 15px;
  }

  .votecover img {
    max-width: 100%;
  }

  .layout__logo {
    background-size:contain!important;
    background-position: center center;
  }

  .portfolio .data-details { width: 100% !important }

  .catcover, .portfolio h4 {
    margin-left: 8px;
  }

  
  .catcover td:first-child,.catcover > td {
     width: 100% !important;
  }

  .catcover tr {
    padding: 5px 5px 5px 10px;
    display: flex;
    flex-direction: column;
    border-bottom: thin solid #eee;
  }

}
