.candidates {
    display: flex !important;
    align-items: center;
    justify-content: space-between !important;
    padding: 20px;
    border-bottom: 1px solid #eee;
}

.candbio {
    display: flex;
    flex:0.8;
    font-weight:600;
    font-size: 13px;
    color: var(--uccaccent-dark);
    text-transform: uppercase;
}

.candbio img {
    height: 100px;
    margin-right:15px;
}

.votecover {
   display: flex;
   flex: 0.2;
}

.votecover img {
    height: 80px;
    margin-right: 15px;
    
}

.votecover button {
   background:var(--uccaccent-light);
   border:3px solid var(--uccmain);
   border-radius: 5px;
   color: var(--uccmain);
   font-size:15px;
   font-weight: bolder;
   cursor: pointer;
}

.portfolio_cover {
  display: flex;
  flex-direction: column;
}

.portfolio {
  width: 100%;
  border-bottom: 1px dashed var(--uccmain);
  padding: 10px 25px;
  margin:10px auto;
  box-shadow: 0 0 2px #ddd inset;
  background: #f5f6f6;
  transition:opacity 0.8s ease-in-out;
}

.portfolio h4 {
  font-size: 11px;
  font-weight: bolder;
  letter-spacing: 0.3em;
  width: fit-content;
  padding: 10px;
  background: #eee;
  box-shadow: 0 0 10px #ddd inset;
  border-radius: 5px;
}

.portfolio_title {
  text-align: center !important;
  display: flex;
  align-items: center;
  font-size: 1.5em;
  line-height: 1.4em;
  color: var(--uccaccent-dark)
}

.vote-btn {
    font-size: 25px;
    font-weight:bolder;
    color: var(--uccmain) !important;
}



.paginator {
    padding: 15px 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.paginator > span {
    padding: 4px 20px;
    background: var(--uccmain);
    box-shadow: 0 0 2px var(--uccmain);
    border-radius: 15px;
    font-size: 13px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    user-select: none;
}

.pageview {
    flex: 0.7;
    display:flex;
    align-items: center;
    justify-content: center
}

.pagehide {
    visibility: hidden;
}

.page_indicator {
    height:20px;
    width: 20px;
    margin:5px;
    border-radius: 50%;
    border:3px solid #ddd;
    background: #f5f5f5;
    box-shadow: 0 1px 3px #666;
    cursor:pointer;
}
.portfolio .data-details { width: 675px }

.complete { background: #02c242; }
.complete_active { background:var(--uccmain) ; border-color:#02c242; }
.current { background: var(--uccmain); }

.catcover {
    border-collapse: collapse;
    width: 95%;
    box-shadow: 0 1px 3px #ddd inset;
    border-radius: 5px;
    background: #fff;
    
}

.catcover tr {
    padding:  5px 15px;
}

.catcover td:first-child {
   width: 15%;
   color: var(--uccmain);
}

.catcover td {
   text-align: left;
}

.search_box { 
   width: 75%;
   margin: 10px;
   padding:5px 1px 5px 15px;
   display: flex;
   align-self: center;
   align-items: center;
   justify-content: space-around;
   border-radius: 35px;
   background: var(--uccmain);
}

.search_box input { 
   width: 92%;
   flex-basis:92%;
   padding: 5px 10px 5px 20px;
   border-radius: 25px;
   border: none;
   box-shadow: 0 0 2px #bbb inset;
   font-weight: 500;
   color:var(--uccaccent-dark);
}
.search_box input:focus,.search_box button { outline: none;}
.search_box input::placeholder {
   color:var(--uccaccent-dark);
}

.search_box button {
  border: none;
  border-radius: 50%;
  background: var(--uccaccent);
  color:#fff;
  cursor: pointer;
}

.profile-btn {
  padding: 2px 5px;
  cursor: pointer;
  border: 1px solid var(--uccmain);
  font-weight: 500;
  font-size: 10px;
  text-transform: uppercase;
  border-radius: 5px;

}
