.candidates {
  display: flex !important;
  align-items: center;
  justify-content: space-between !important;
  padding: 20px;
  border-bottom: 1px solid #eee;
}

.candbio {
  display: flex;
  flex: 0.8;
  font-weight: 600;
  font-size: 13px;
  color: var(--uccaccent-dark);
  text-transform: uppercase;
}

.candbio img {
  height: 100px;
  margin-right: 15px;
}

.votecover {
  display: flex;
  flex: 0.2;
}

.candbio select {
  width: 80px !important;
  height: 20px !important;
  padding: 2px 10px;
  line-height: 10px;
  font-size: 0.8rem;
  font-weight: bolder;
  color: #02c242;
  border: 2px solid #02c242;
}

.votecover img {
  height: 80px;
  margin-right: 15px;
}

.votecover button {
  background: var(--uccaccent-light);
  border: 3px solid var(--uccmain);
  border-radius: 5px;
  color: var(--uccmain);
  font-size: 15px;
  font-weight: bolder;
  cursor: pointer;
}

.portfolio_cover {
  display: flex;
  flex-direction: column;
}

.elector_cover {
  height: 32vw;
  overflow-y: scroll;
}

.portfolio {
  width: 100%;
  border-bottom: 1px dashed var(--uccmain);
  padding: 10px 25px;
  margin: 10px auto;
  box-shadow: 0 0 2px #ddd inset;
  background: #f5f6f6;
  transition: opacity 0.8s ease-in-out;
}

.portfolio_title {
  text-align: center !important;
  display: flex;
  align-items: center;
  font-size: 1.5em;
  line-height: 1.4em;
  color: var(--uccaccent-dark);
}

.vote-btn {
  font-size: 25px;
  font-weight: bolder;
  color: var(--uccmain) !important;
}

.paginator {
  padding: 15px 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.paginator > span {
  padding: 4px 20px;
  background: var(--uccmain);
  box-shadow: 0 0 2px var(--uccmain);
  border-radius: 15px;
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  user-select: none;
}

.pageview {
  flex: 0.7;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagehide {
  visibility: hidden;
}

.page_indicator {
  height: 20px;
  width: 20px;
  margin: 5px;
  border-radius: 50%;
  border: 3px solid #ddd;
  background: #f5f5f5;
  box-shadow: 0 1px 3px #666;
  cursor: pointer;
}
.portfolio .data-details {
  width: 675px;
}

.complete {
  background: #02c242;
}
.complete_active {
  background: var(--uccmain);
  border-color: #02c242;
}
.current {
  background: var(--uccmain);
}

.cursor {
  cursor: pointer;
}

.form-cover {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 20px;
}

.form-cover button {
  border: 2px solid #263876;
  background: #263876 /*#ffee1f*/;
  border-radius: 0 5px 5px 0;
  padding: 0 15px;
  font-weight: bolder;
  color: #fff;
}

.form-cover .vcount {
  border-radius: 15px;
}

.form-cover input {
  border: 2px solid #263876;
  border-right: none;
  border-radius: 5px 0 0 5px;
  padding: 0 15px;
  text-align: center;
  font-weight: 800;
}

.form-cover input:focus {
  outline: none;
}

.form-cover input::placeholder {
  font-size: 0.7rem;
  text-align: center;
  font-weight: 500;
  color: #263876;
}
.rbox {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}
.rbox input {
  margin: 2px 5px;
  border: 2px solid #263876;
  border-radius: 5px;
}

.rselect {
  width: 200px;
  height: 30px !important;
  margin: 2px 5px;
  border: 2px solid #263876;
  background-color: rgb(204, 235, 250);
  border-radius: 5px;
  padding: 0 15px;
  text-align: center;
  font-weight: 500;
  font-size: 11px;
  color: #263876;
}
button.rbutton {
  width: 200px;
  height: 30px !important;
  margin: 2px 5px;
  border: 2px solid #263876;
  border-radius: 5px !important;
  padding: 0 15px;
  text-align: center;
  font-weight: 800;
  font-size: 16px;
  background-color: #263876;
  color: #fff !important;
}
